.react-joystick .arrows-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.react-joystick .arrows-wrapper .controller-arrow {
  display: inline-flex;
  position: absolute;
}
.react-joystick .arrows-wrapper .arrow-up {
  display: inline-flex;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 50%);
}
.react-joystick .arrows-wrapper .arrow-down {
  display: inline-flex;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}
.react-joystick .arrows-wrapper .arrow-left {
  display: inline-flex;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(50%, -50%);
}
.react-joystick .arrows-wrapper .arrow-right {
  display: inline-flex;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(-50%, -50%);
}
