.react-joystick-ghost-area {
  position: relative;
}
.react-joystick-ghost-area .react-joystick-wrapper {
  position: absolute;
  width: 0;
  height: 0;
}
.react-joystick-ghost-area .react-joystick-wrapper .react-joystick {
  transform: translate(-50%, -50%);
}
.react-joystick-ghost-area .react-joystick-wrapper .react-joystick.ghost {
  visibility: hidden;
}
.react-joystick-ghost-area .react-joystick-wrapper .react-joystick.ghost-active {
  visibility: visible;
}
